/**
 * Makes a PUT request using given URL and options
 *
 * @function
 * @param {string} url url to put
 * @param {FetchRequestOptions} [options] Options to be used when making a fetch call.
 * @returns {FetchResponse} A Promise that resolves to the data returned by the URL or an error if the request failed
 */
export const put = async (url, options) => {
    return makeRequestWithMethod(url, options, 'PUT');
};

export const upload = async () => {};
