import handleResponse, { handleError } from './handleResponse';
import { getRequestBody, getURLWithParams } from './helper';

const globalHeaders = {};
const makeRequest = async (url, options = {}) => {
    const {
        mode = 'cors',
        credentials = 'include',
        headers = {},
        method,
        abortControllerCallback,
        timeout,
    } = options ?? {};

    const urlWithParams = getURLWithParams(url, options);
    const bodyToSend = getRequestBody(options);

    const controller = new AbortController();
    const { signal } = controller;

    let requestTimedout = false;
    let timeoutId;

    const fetchOptions = {
        mode: mode ?? 'cors',
        credentials: credentials ?? 'include',
        headers: new Headers({ ...globalHeaders, ...(headers ?? {}) }),
        body: bodyToSend,
        method,
        signal,
    };

    if (typeof abortControllerCallback === 'function') {
        abortControllerCallback(controller);
    }

    if (timeout) {
        timeoutId = setTimeout(() => {
            requestTimedout = true;
            controller.abort();
        }, timeout);
    }

    try {
        const httpResponse = await fetch(urlWithParams, fetchOptions);
        const response = await handleResponse(httpResponse);

        return response;
    } catch (err) {
        handleError(err, requestTimedout);
    } finally {
        clearTimeout(timeoutId);
    }

    return undefined;
};

export default makeRequest;
