/**
 * Contains more information about the failure of a fetch request
 * @typedef {object} FetchErrorDetails
 * @property {ABORT_ERROR|RESPONSE_ERROR|FETCH_ERROR|TIMEDOUT_ERROR} type - Type of the error
 * @property {Response} [response] The {@link https://developer.mozilla.org/en-US/docs/Web/API/Response | Response } object if there is one.
 * @property {Object|String} [responseData] The data returned by the request.
 * @property {String} [status] The HTTP Status of the request
 */

/**
 * @class
 * Error thrown when a Fetch API call fails
 * @property {FetchErrorDetails} errorDetails object containing details of the error
 */
function FetchError(errorDetails) {
    const instance = new Error('fetch error');
    Object.setPrototypeOf(instance, Object.getPrototypeOf(this));

    instance.errorDetails = errorDetails;

    return instance;
}
FetchError.prototype = Object.create(Error.prototype, {
    constructor: {
        value: Error,
        enumerable: false,
        writable: true,
        configurable: true,
    },
});

Object.setPrototypeOf(FetchError, Error);

export default FetchError;
